import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import "./MapStyle.scss";
import ReactSearchBox from "react-search-box";
import Dexie from "dexie";
import x from "../../Assets/times.svg";
import skalaKolorow from "../../Assets/group-3.svg";
import skalaKolorowMobile from "../../Assets/skala_kol_mobile.svg";
import L from "leaflet";
import angle from "../../Assets/angle-down.svg";
import {
  Map as MapContainer,
  Marker,
  Tooltip,
  TileLayer,
  Circle,
  ScaleControl,
  ZoomControl,
} from "react-leaflet";

const kolor00cc66 = new L.Icon({
  iconUrl: require("./../../Assets/kolor_very_low.svg"),
  iconRetinaUrl: require("./../../Assets/kolor_very_low.svg"),
  iconAnchor: null,
  popupAnchor: [0, 0],
  shadowUrl: null,
  shadowSize: null,
  shadowAnchor: null,
  iconSize: new L.Point(30, 39),
  className: "leaflet-div-icon",
});

const kolor92d050 = new L.Icon({
  iconUrl: require("./../../Assets/kolor_low.svg"),
  iconRetinaUrl: require("./../../Assets/kolor_low.svg"),
  iconAnchor: null,
  popupAnchor: [0, 0],
  shadowUrl: null,
  shadowSize: null,
  shadowAnchor: null,
  iconSize: new L.Point(30, 39),
  className: "leaflet-div-icon",
});
const kolorffcc00 = new L.Icon({
  iconUrl: require("./../../Assets/kolor_medium.svg"),
  iconRetinaUrl: require("./../../Assets/kolor_medium.svg"),
  iconAnchor: null,
  popupAnchor: [0, 0],
  shadowUrl: null,
  shadowSize: null,
  shadowAnchor: null,
  iconSize: new L.Point(30, 39),
  className: "leaflet-div-icon",
});
const kolorff9900 = new L.Icon({
  iconUrl: require("./../../Assets/kolor_high.svg"),
  iconRetinaUrl: require("./../../Assets/kolor_high.svg"),
  iconAnchor: null,
  popupAnchor: [0, 0],
  shadowUrl: null,
  shadowSize: null,
  shadowAnchor: null,
  iconSize: new L.Point(30, 39),
  className: "leaflet-div-icon",
});
const kolor800000 = new L.Icon({
  iconUrl: require("./../../Assets/kolor_very_high.svg"),
  iconRetinaUrl: require("./../../Assets/kolor_very_high.svg"),
  iconAnchor: null,
  popupAnchor: [0, 0],
  shadowUrl: null,
  shadowSize: null,
  shadowAnchor: null,
  iconSize: new L.Point(30, 39),
  className: "leaflet-div-icon",
});
const Map = () => {
  const [lat, latChange] = useState(50.1093);
  const [lng, lngChange] = useState(18.836207);
  const [dataToMap, dataToMapChange] = useState([]);
  const [zoom, zoomChange] = useState(15);
  const [shownDropDown, shownDropDownChange] = useState(true);
  const [inputValue, inputValueChange] = useState("Miasto, ulica");
  const [shownPopup, shownPopupChange] = useState(false);
  const [miejsce, miejsceChange] = useState("");
  const [pomiar, pomiarChange] = useState(null);
  const [dataPomiaru, dataPomiaruChange] = useState("");
  const [wykres, wykresChange] = useState(
    "http://srodowiskowokolicy.pl/9CA72FC4F5FC.jpg"
  );
  const [width, widthChange] = useState(0);
  const [data, dataChange] = useState([
    {
      key: "Wrocław",
      value: "Gostyń, Rybnicka",
      positionDlugosc: 50.1093,
      positionSzerokosc: 18.836207,
      pomiar: 20,
      kolor: "#92d050",
    },
  ]);
  const [actualCoord, actualCoordChange] = useState([
    {
      positionDlugosc: 6,
      positionSzerokosc: 25,
    },
  ]);
  useEffect(() => {
    setInterval(getPHP(), 3000);
    window.addEventListener("resize", updateWindowDimensions);
    updateWindowDimensions();
    scrollTo();
    return function cleanup() {
      window.removeEventListener("resize", updateWindowDimensions);
    };
  });

  useEffect(() => {
    indexedDB();
  }, [dataPomiaru]);

  const scrollTo = () => {
    try {
      window.scroll({
        top: 0,
        left: 0,
        behavior: "smooth",
      });
    } catch (error) {
      window.scrollTo(0, 0);
    }
  };
  const clearInput = () => {
    inputValueChange("");
  };

  const updateWindowDimensions = () => {
    widthChange(window.innerWidth);
  };
  const showDropDown = () => {
    if (shownDropDown) {
      var arrayOfElements = document.querySelectorAll(".dropdown li");
      var lengthOfArray = arrayOfElements.length;

      for (var i = 0; i < lengthOfArray; i++) {
        arrayOfElements[i].style.display = "block";
      }
    } else {
      var arrayOfElements = document.querySelectorAll(".dropdown li");
      var lengthOfArray = arrayOfElements.length;

      for (var i = 0; i < lengthOfArray; i++) {
        arrayOfElements[i].style.display = "none";
      }
    }

    shownDropDownChange((shownDropDown) => !shownDropDown);
  };
  const showSensor = (value) => {
    showPopup();
    showDropDown();
    let copyActualCoord = {};
    copyActualCoord.positionSzerokosc = value.positionSzerokosc;
    copyActualCoord.positionDlugosc = value.positionDlugosc;
    actualCoordChange(copyActualCoord);
    pomiarChange(value.pomiar);
    miejsceChange(value.value);
    lngChange(value.positionSzerokosc);
    latChange(value.positionDlugosc);
    dataPomiaruChange(value.dataPomiaru);
    wykresChange(
      "http://srodowiskowokolicy.pl/" + value.nazwa_stacji + ".jpg "
    );
    setTimeout(getPHP(), 1000);
  };

  const showPopup = () => {
    shownPopupChange(true);
  };
  const showPopupWithUpdate = (value) => {
    let copyActualCoord = {};
    copyActualCoord.positionSzerokosc = value.szerokosc;
    copyActualCoord.positionDlugosc = value.dlugosc;
    actualCoordChange(copyActualCoord);
    pomiarChange(value.pomiar);
    miejsceChange(value.nazwa_czujnika);
    lngChange(value.szerokosc);
    latChange(value.dlugosc);
    dataPomiaruChange(value.data);
    wykresChange(
      "http://srodowiskowokolicy.pl/" + value.nazwa_stacji + ".jpg "
    );
    shownPopupChange(true);
  };

  const hidePopup = () => {
    shownPopupChange(false);
  };
  const indexedDB = () => {
    var db = new Dexie("CzujnikiHalas");
    db.version(1).stores({
      halas: "++id,miejsce",
    });
    db.halas.clear();
    db.halas
      .add({
        miejsce: miejsce,
      })
      .catch(function (e) {
        alert("Error: " + (e.stack || e));
      });
  };

  const getPHP = () => {
    const url = "http://srodowiskowokolicy.pl/wspolne_halas_json.php";
    fetch(url)
      .then((response) => response.text())
      .then((contents) => sensorsMap(contents))
      .catch(() => {
        secondFetch();
      });
  };
  const sensorsMap = (contents) => {
    let txt = document.createElement("textarea");
    txt.innerHTML = contents;
    contents = txt.value;
    let contentsToJSON = JSON.parse(contents);
    let data1 = { ...data };

    updateFromDefault(contentsToJSON);
    for (let i = 0; i <= 3; i++) {
      data1[i].key = contentsToJSON["czujniki"][i].nazwa_czujnika;
      data1[i].value = decodeURIComponent(
        contentsToJSON["czujniki"][i].nazwa_czujnika
      );
      data1[i].positionSzerokosc = contentsToJSON["czujniki"][i].szerokosc;
      data1[i].positionDlugosc = contentsToJSON["czujniki"][i].dlugosc;
      data1[i].pomiar = contentsToJSON["czujniki"][i].pomiar;
      data1[i].dataPomiaru = contentsToJSON["czujniki"][i].data;

      data1[i].kolor = contentsToJSON["czujniki"][i].kolor.replace(
        "#",
        "kolor"
      );
      data1[i].nazwa_stacji = contentsToJSON["czujniki"][i].nazwa_stacji;
    }

    dataChange(data1);
  };

  const updateFromDefault = (contentsToJSON) => {
    var db = new Dexie("CzujnikiHalas");
    db.version(1).stores({
      halas: "++id,miejsce",
    });

    const getPosts = async () => {
      const allPosts = await db.halas.toArray();

      if (allPosts.length > 0) {
        for (let i = 0; i < contentsToJSON["czujniki"].length; i++) {
          if (
            contentsToJSON["czujniki"][i].nazwa_czujnika == allPosts[0].miejsce
          ) {
            let copyActualCoord = {};
            copyActualCoord.positionSzerokosc =
              contentsToJSON["czujniki"][i].dlugosc;
            copyActualCoord.positionDlugosc =
              contentsToJSON["czujniki"][i].szerokosc;
            pomiarChange(contentsToJSON["czujniki"][i].pomiar);
            wykresChange(
              "http://srodowiskowokolicy.pl/" +
                contentsToJSON["czujniki"][i].nazwa_stacji +
                ".jpg"
            );
            dataPomiaruChange(contentsToJSON["czujniki"][i].data);
            miejsceChange(contentsToJSON["czujniki"][i].nazwa_czujnika);
            latChange(contentsToJSON["czujniki"][i].dlugosc);
            lngChange(contentsToJSON["czujniki"][i].szerokosc);
          }
        }
      } else {
        let copyActualCoord = {};
        copyActualCoord.positionSzerokosc =
          contentsToJSON["czujniki"][0].szerokosc;
        copyActualCoord.positionDlugosc = contentsToJSON["czujniki"][0].dlugosc;
        actualCoordChange(copyActualCoord);
        pomiarChange(contentsToJSON["czujniki"][0].pomiar);
        miejsceChange(contentsToJSON["czujniki"][0].nazwa_czujnika);
        if (wykres !== contentsToJSON["czujniki"][0].nazwa_stacji)
          wykresChange(
            "http://srodowiskowokolicy.pl/" +
              contentsToJSON["czujniki"][0].nazwa_stacji +
              ".jpg"
          );
        dataPomiaruChange(contentsToJSON["czujniki"][0].data);
      }
    };

    getPosts();

    contentsToJSON["czujniki"].sort(function (a, b) {
      if (a.nazwa_czujnika > b.nazwa_czujnika) {
        return 1;
      }
      if (b.nazwa_czujnika > a.nazwa_czujnika) {
        return -1;
      }
      return 0;
    });

    dataToMapChange(contentsToJSON["czujniki"]);
  };
  const secondFetch = () => {
    if (data[0].kolor == "kolor") {
      //const proxyurl = "https://cors-anywhere.herokuapp.com/";
      const url = "https://srodowiskowokolicy.pl/wspolne_halas_json.php"; // site that doesn’t send Access-Control-*
      fetch(url)
        .then((response) => response.text())
        .then((contents) => sensorsMap(contents))
        .catch(() =>
          console.log("Can’t access " + url + " response. Blocked by browser?")
        );
    }
  };

  const toMap = (value) => {
    showPopup();
    let copyActualCoord = {};
    copyActualCoord.positionSzerokosc = value.positionSzerokosc;
    copyActualCoord.positionDlugosc = value.positionDlugosc;
    actualCoordChange(copyActualCoord);
    pomiarChange(value.pomiar);
    miejsceChange(value.value);
    lngChange(value.positionSzerokosc);
    latChange(value.positionDlugosc);
    dataPomiaruChange(value.dataPomiaru);
    wykresChange(
      "http://srodowiskowokolicy.pl/" + value.nazwa_stacji + ".jpg "
    );
    getPHP();
  };
  const showFromMap = (value) => {
    pomiarChange(value.pomiar);
    miejsceChange(value.nazwa_czujnika);
    dataPomiaruChange(value.data);
    wykresChange(
      "http://srodowiskowokolicy.pl/" + value.nazwa_stacji + ".jpg "
    );
    window.scrollTo(0, 400);
    getPHP();
  };

  const position = [lat, lng];
  return (
    <section className="map">
      <ul className="map__menu">
        <li className="map__menu__element active">
          <NavLink
            className="map__menu__element__link"
            to="/map"
            activeClassName="active"
          >
            Hałas
          </NavLink>
        </li>
        <li className="map__menu__element">
          <NavLink className="map__menu__element__link" to="/mapPowietrze">
            Powietrze
          </NavLink>
        </li>
      </ul>
      <div className="map__search">
        <ReactSearchBox
          placeholder="Miasto, ulica"
          value={inputValue}
          data={data}
          onSelect={toMap}
          onClick={clearInput}
          onFocus={() => {
            document.getElementsByClassName("sc-bdVaJa")[0].value = "";
          }}
        />
      </div>
      <MapContainer center={position} zoom={zoom}>
        <ScaleControl />
        <ZoomControl />
        {width > 800 ? (
          <img className="map__scale" src={skalaKolorow} />
        ) : (
          <img className="map__scale" src={skalaKolorowMobile} />
        )}
        <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {dataToMap.length >= data.length
          ? dataToMap.map((czujnik) => {
              return (
                <div key={czujnik.nazwa_czujnika}>
                  {czujnik.kolor == "#92d050" ? (
                    <div>
                      <Circle
                        center={[czujnik.dlugosc, czujnik.szerokosc]}
                        fillColor={czujnik.kolor}
                        radius={500}
                        weight={0}
                      />
                      <Marker
                        icon={kolor92d050}
                        position={[czujnik.dlugosc, czujnik.szerokosc]}
                        onClick={() => showPopupWithUpdate(czujnik)}
                      >
                        <Tooltip>
                          {czujnik.nazwa_czujnika} <br />{" "}
                          <span style={{ color: czujnik.kolor }}>
                            {czujnik.pomiar} dBA
                          </span>
                        </Tooltip>
                      </Marker>
                    </div>
                  ) : czujnik.kolor == "#00cc66" ? (
                    <div>
                      <Circle
                        center={[czujnik.dlugosc, czujnik.szerokosc]}
                        fillColor={czujnik.kolor}
                        radius={500}
                        weight={0}
                      />
                      <Marker
                        icon={kolor00cc66}
                        position={[czujnik.dlugosc, czujnik.szerokosc]}
                        onClick={() => showPopupWithUpdate(czujnik)}
                      >
                        <Tooltip>
                          {czujnik.nazwa_czujnika} <br />{" "}
                          <span style={{ color: czujnik.kolor }}>
                            {czujnik.pomiar} dBA
                          </span>{" "}
                        </Tooltip>
                      </Marker>
                    </div>
                  ) : czujnik.kolor == "#ffcc00" ? (
                    <div>
                      <Circle
                        center={[czujnik.dlugosc, czujnik.szerokosc]}
                        fillColor={czujnik.kolor}
                        radius={500}
                        weight={0}
                      />
                      <Marker
                        icon={kolorffcc00}
                        position={[czujnik.dlugosc, czujnik.szerokosc]}
                        onClick={() => showPopupWithUpdate(czujnik)}
                      >
                        <Tooltip>
                          {czujnik.nazwa_czujnika} <br />{" "}
                          <span style={{ color: czujnik.kolor }}>
                            {czujnik.pomiar} dBA
                          </span>{" "}
                        </Tooltip>
                      </Marker>
                    </div>
                  ) : czujnik.kolor == "#ff9900" ? (
                    <div>
                      <Circle
                        center={[czujnik.dlugosc, czujnik.szerokosc]}
                        fillColor={czujnik.kolor}
                        radius={500}
                        weight={0}
                      />
                      <Marker
                        icon={kolorff9900}
                        position={[czujnik.dlugosc, czujnik.szerokosc]}
                        onClick={() => showPopupWithUpdate(czujnik)}
                      >
                        <Tooltip>
                          {czujnik.nazwa_czujnika} <br />{" "}
                          <span style={{ color: czujnik.kolor }}>
                            {czujnik.pomiar} dBA
                          </span>{" "}
                        </Tooltip>
                      </Marker>
                    </div>
                  ) : czujnik.kolor == "#800000" ? (
                    <div>
                      <Circle
                        center={[czujnik.dlugosc, czujnik.szerokosc]}
                        fillColor={czujnik.kolor}
                        radius={500}
                        weight={0}
                      />
                      <Marker
                        icon={kolor800000}
                        position={[czujnik.dlugosc, czujnik.szerokosc]}
                        onClick={() => showPopupWithUpdate(czujnik)}
                      >
                        <Tooltip>
                          {czujnik.nazwa_czujnika} <br />{" "}
                          <span style={{ color: czujnik.kolor }}>
                            {czujnik.pomiar} dBA
                          </span>{" "}
                        </Tooltip>
                      </Marker>
                    </div>
                  ) : null}
                </div>
              );
            })
          : null}
      </MapContainer>
      <div className="dropdown-container">
        <div className="dropdown__description" onClick={() => showDropDown()}>
          <div className="hideMobile">Lista czujników</div>
          <img className="dropdown__description__icon" src={angle} />
        </div>
        <ul className="dropdown">
          {data.map((czujnik, index) => {
            return (
              <li
                className="dropdown__element"
                key={index}
                onClick={() => showSensor(czujnik)}
              >
                {czujnik.value}
              </li>
            );
          })}
        </ul>
      </div>

      {shownPopup ? (
        <div className="popup" id="popupDane">
          <div className="popup__dane">
            <div className="popup__dane__place">{miejsce}</div>
            <div className="popup__dane__measure">
              <div className="popup__dane__measure__key">Data odczytu</div>
              <div className="popup__dane__measure__value">{dataPomiaru}</div>
            </div>
            <div className="popup__dane__measure">
              <div className="popup__dane__measure__key">Poziom hałasu</div>
              <div className="popup__dane__measure__value">{pomiar} dBA</div>
            </div>
          </div>
          <a
            className="popup_wykres"
            onClick={() => window.open(wykres, "_blank")}
          >
            sprawdź wykres
          </a>
          <img className="popup__close" src={x} onClick={() => hidePopup()} />
        </div>
      ) : null}
    </section>
  );
};
export default Map;
